<template>
  <div class="ao-asset-money">
    <ao-asset-return></ao-asset-return>
    <div class="content">
      <div class="header">
        <div class="col">
          <div>{{ address_title }}</div>
          <el-select v-model="value" :placeholder="address_txt[0]">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </div>
        <div class="col col-5">
          <div>{{ address_txt[1] }}</div>
          <el-input v-model="address" :placeholder="address_txt[2]"></el-input>
        </div>
        <div class="col col-5">
          <div>{{ address_txt[3] }}</div>
          <el-input v-model="message" :placeholder="address_txt[2]"></el-input>
        </div>
      </div>
      <div class="btn">
        <el-button type="primary" @click="add">{{  address_btn[0] }}</el-button>
        <el-button v-show="showSave" type="primary" @click="save">{{  address_btn[1] }}</el-button>
      </div>
      <!-- 表格 -->
      <!-- 表格 -->
      <div class="title">{{ address_txt[4] }}</div>
      <div class="table">
        <div class="theader">
          <div class="th">
            <div class="td left">{{ address_td[0] }}</div>
            <div class="td td-3 center">{{ address_td[1] }}</div>
            <div class="td td-3 center">{{ address_td[2] }}</div>
            <div class="td td-2 right">{{ address_td[3] }}</div>
          </div>
        </div>
        <div class="tbody">
          <div class="tr border" v-for="(item, index) in list" :key="index">
            <div class="td left">{{ item.currency }}</div>
            <div class="td td-3 center">{{ item.address }}</div>
            <div class="td td-3 center">{{ item.message }}</div>
            <div class="td td-2 right">
              <span class="btn" @click="del(index)">{{  address_btn[2] }}</span>
              <span class="btn" @click="edit(index)">{{  address_btn[3] }}</span>
            </div>
          </div>
        </div>
        <div class="more" @click="more">{{ is_down ? $t('person.no_more') : $t('person.more') }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import AoAssetReturn from "./components/AoAssetReturn";
import {
  getCurrency,
  getAddressList,
  addAddress,
  editAddress,
  delAddress
} from "@/api/asset";

export default {
  components: {
    AoAssetReturn
  },
  data() {
    return {
      address_title: this.$t('asset.address_title'),
      address_txt: this.$t('asset.address_txt'),
      address_btn: this.$t('asset.address_btn'),
      address_td: this.$t('asset.address_td'),

      options: [],
      value: "",
      address: "",
      message: "",
      id: 1,
      page: 1,
      list: [],
      is_down: false,
      showSave: false
    };
  },
  mounted() {
    this.id = this.$store.state.__data.id;
    this.getAddressList();
    getCurrency({ id: this.id }).then(res => {
      res.unshift({
        id: -1,
        name: "USDT"
      });
      this.options = res.map(o => {
        return {
          value: o.id,
          label: o.name
        };
      });
    });
  },
  methods: {
    add() {
      this.showSave = false;
      var data = {
        type: this.id,
        value: this.value,
        address: this.address,
        message: this.message
      };
      addAddress(data).then(res => {
        this.list.unshift(res);
      });
    },
    del(index) {
      var id = this.list[index].id;
      delAddress({ id }).then(res => {
        this.list.splice(index, 1);
      });
    },
    edit(index) {
      this.showSave = true;
      this.saveIndex = index;
      var item = this.list[this.saveIndex];
      var obj = this.options.find(o => o.label == item.currency);
      this.value = obj.value;
      this.address = item.address;
      this.message = item.message;
    },
    save() {
      var data = {
        id: this.list[this.saveIndex].id,
        type: this.id,
        value: this.value,
        address: this.address,
        message: this.message
      };
      editAddress(data).then(res => {
        this.showSave = false;
        this.list[this.saveIndex] = res;
      });
    },
    more() {
      if (this.is_down == true) return;
      this.page++;
      this.getAddressList();
    },
    getAddressList() {
      getAddressList({ id: this.id, page: this.page }).then(res => {
        if (res.length > 0) {
          this.list = this.list.concat(res);
        } else {
          this.is_down = true;
        }
      });
    }
  }
};
</script>

<style lang="scss">
.ao-asset-money {
  .content {
    width: 100%;
    padding: 0 10px;
    .header {
      color: #c7cce6;
      display: flex;
    }
    .col {
      flex: 1;
      & + .col {
        margin-left: 20px;
      }
      & > div {
        font-size: 12px;
        color: #c7cce6;
        margin-bottom: 10px;
      }
      &.col-5 {
        flex: 4;
      }
      .el-input .el-input__inner {
        padding: 10px;
        border: 1px solid #4e5b85;
        font-size: 18px;
        color: #c7cce6;
        border-radius: 3px;
        background-color: transparent;
        outline: none;
        font-size: 14px;
      }
    }
    .btn {
      text-align: right;
      margin-top: 16px;
    }
    .el-button {
      flex: 1;
      width: 200px;
      line-height: 46px;
      background-color: #7a98f7;
      border-radius: 4px;
      color: #fff;
      border: none;
      padding: 0;
    }
    .title {
      color: #fff;
      margin-left: 10px;
    }
    .table {
      margin: 10px;
      padding: 0 20px;
      background: #171c2b;
      .theader,
      .tbody {
        padding: 0 20px;
      }
      .th,
      .tr {
        display: flex;
      }
      .th {
        line-height: 1.5;
      }
      .th .td,
      .tr .td {
        color: #728daf;
        font-size: 12px;
        flex: 1;
        &-2 {
          flex: 2;
        }
        &-3 {
          flex: 3;
        }
      }
      .th .td,
      .tr .td {
        &.left {
          text-align: left;
        }
        &.center {
          text-align: center;
        }
        &.right {
          text-align: right;
        }
      }
      .th .td {
        font-size: 14px;
        color: #fff;
        line-height: 3;
      }
      .tr .td {
        .btn {
          color: #5697f4;
          font-size: 12px;
          cursor: pointer;
          & + .btn {
            padding-left: 5px;
          }
        }
      }
      .tr {
        padding: 15px 0;
        &.border {
          border-top: 1px solid #1e2c42;
        }
      }
      .more {
        color: #c7cce6;
        font-size: 12px;
        width: 100%;
        line-height: 40px;
        text-align: center;
        cursor: pointer;
      }
    }
  }
}
.el-select-dropdown .el-scrollbar > div {
  overflow-x: hidden;
}
</style>



